<template>
  <div class="rounded-lg bg-white p-2">
    <div class="pb-2">wall visit home</div>
    <div
      class="grid md:grid-cols-4 grid-cols-2 gap-2"
      v-if="listWallVisit.length > 0"
    >
      <div
        class="rounded-lg shadow-xl overflow-hidden"
        v-for="data in listWallVisit"
        :key="data.rid"
      >
        <button @click="onClick(data.rid)">
          <img
            class="w-full h-64 object-cover"
            :src="$store.makePicUrl(data.wall_url)"
          />
        </button>
        <div class="flex space-x-3 items-center">
          <div class="text-left font-bold">{{ data.wall_name }}</div>
          <div class="text-gray-600 text-sm">{{ data.user_name }}</div>
          <img
            v-if="data.is_send"
            class="w-6 h-6"
            src="/images/hero-gift.svg"
          />
        </div>
        <div class="text-xs text-left">{{ $store.getAgo(data.last_edit) }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      errorMessage: "",
      listWallVisit: [],
    };
  },
  async mounted() {
    const reqData = {
      api: "getWallVisitList",
      //   loginToken: localStorage.getItem("loginToken"),
      userUid: this.$store.getters.userUid,
    };
    this.loading = true;
    const res = await this.$store.callAsync(reqData);
    this.loading = false;
    if (res.code != 100) {
      console.log("getWallList fail", res);
      this.errorMessage = res.message;
      return;
    }

    console.log("getWallVisitList ok", reqData, res);

    this.listWallVisit = [];
    res.listWallVisit.forEach((row) => {
      console.log("row", row);
      this.listWallVisit.push({
        rid: row.rid,
        wall_name: row.wall_name,
        wall_url: row.wall_url,
        last_edit: this.$store.getMoment(row.dt_edit),
        user_name: row.user_name,
        is_send: row.is_send,
      });
    });
  },
  methods: {
    onClick(rid) {
      this.$router.push(`/visit/${rid}`);
    },
  },
};
</script>
